import { Entity } from '../entities/Entity';
import { Coordinate } from '../space/Coordinate';

export class Job {
	entity: Entity;
	constructor(entity: Entity) {
		this.entity = entity;
	}
	start() {
		throw new Error(`Not implemented for ${this.constructor.name}`);
	}
}

export class PatrolJob extends Job {
	waypoints: Coordinate[];
	waypointIndex: number;

	constructor(entity: Entity, waypoints: Coordinate[]) {
		super(entity);
		if (waypoints.length < 2) {
			throw new Error('A patrol must have at least 2 waypoints');
		}
		this.waypoints = waypoints;
		this.waypointIndex = 0;
	}

	start() {
		const destroyers = [
			this.entity.moveRest.on(() => {
				this.waypointIndex = (this.waypointIndex + 1) % this.waypoints.length;
				this.entity.moveTo(this.waypoints[this.waypointIndex]);
			})
		];

		this.entity.moveTo(this.waypoints[0]);
		return () => destroyers.forEach(d => d());
	}
}
