import Color from 'color';
import React, { FunctionComponent, useEffect, useMemo, useState } from 'react';
import { EntityComponent } from '../entities/Entity';
import { GuardEntity } from '../entities/GuardEntity';
import { PatrolJob } from '../jobs/Patrol';
import { RandomScene } from '../scene/Scene';
import { Anchor } from '../space/Anchor';
import { Coordinate } from '../space/Coordinate';
import { MonochromeBox } from '../space/MonochromeBox';
import { distanceToCameraComparator } from '../space/PERSPECTIVE';
import { Viewport } from '../space/Viewport';
import { ActiveEntityOverlay } from '../ui/ActiveEntityOverlay';

const HelloWorldSceneTerrain: FunctionComponent<
	{
		coordinate: Coordinate;
	} & (typeof MonochromeBox extends FunctionComponent<infer T> ? T : {})
> = ({ coordinate: coord, ...monochromeBoxProps }) => {
	const [isHovered, setIsHovered] = useState(false);
	const translated = Coordinate.clone(coord).transform(-0.5, -0.5, -1);
	return (
		<Anchor key={translated.toString()} x={translated.x} y={translated.y} z={translated.z}>
			<MonochromeBox
				{...monochromeBoxProps}
				fill={isHovered ? Color('#282c34').lighten(1) : undefined}
				stroke={isHovered ? Color('#fff') : undefined}
				innerStroke={isHovered ? Color('#ccd1dd') : undefined}
				onMouseEnter={() => setIsHovered(true)}
				onMouseLeave={() => setIsHovered(false)}
			/>
		</Anchor>
	);
};

const Demo: FunctionComponent = () => {
	const size = 40;
	const scene = useMemo(() => {
		const scene = new RandomScene(size, size);

		const randomLocation = scene.terrain[Math.floor(Math.random() * scene.terrain.length)];
		const guard = new GuardEntity('garrd', randomLocation);
		guard.doJob(
			new PatrolJob(guard, [
				scene.terrain[Math.floor(Math.random() * scene.terrain.length)],
				scene.terrain[Math.floor(Math.random() * scene.terrain.length)],
				scene.terrain[Math.floor(Math.random() * scene.terrain.length)],
				scene.terrain[Math.floor(Math.random() * scene.terrain.length)]
			])
		);
		scene.entities.push(guard);

		return scene;
	}, []);

	useEffect(() => {
		console.log('--- Scene#play ---');
		return scene.play();
	}, [scene]);
	const centerIndex = Math.floor((size + 1 - (size % 2)) * (size / 2));
	const [centerCoord, setCenterCoord] = useState(scene.terrain[centerIndex]);

	const zoomFactor = 1;

	const terrain = useMemo(
		() =>
			scene.terrain
				.slice()
				.sort(distanceToCameraComparator)
				.map(coord => (
					<HelloWorldSceneTerrain
						key={coord.toString()}
						coordinate={coord}
						onClick={event => {
							event.preventDefault();
							setCenterCoord(coord);
						}}
					/>
				)),
		[scene.terrain]
	);

	const entities = useMemo(
		() =>
			scene.entities
				.slice()
				.sort((a, b) => distanceToCameraComparator(a.location, b.location))
				.map(entity => <EntityComponent key={entity.id} entity={entity} />),
		[scene.entities]
	);

	return (
		<>
			<Viewport center={centerCoord} zoom={zoomFactor}>
				<g id="scene-terrain">{terrain}</g>
				<g id="scene-entities">{entities}</g>
			</Viewport>
			<ActiveEntityOverlay />
		</>
	);
};
export default Demo;
