import Color from 'color';
import React, { FunctionComponent } from 'react';
import { Anchor } from '../space/Anchor';
import { Coordinate } from '../space/Coordinate';
import { MonochromeTile } from '../space/MonochromeTile';
import { Entity } from './Entity';

const zoom = 0.5;
const translate = zoom / 2;
const offset = new Coordinate(-translate, -translate, 0);

export class GuardEntity extends Entity {
	Component: FunctionComponent = () => {
		return (
			<Anchor {...offset}>
				<MonochromeTile zoom={zoom} fill={Color('#ff0000')} />
			</Anchor>
		);
	};
}
