import { FunctionComponent, useCallback, useState } from 'react';
import { Job } from '../jobs/Patrol';
import { MovingAnchor } from '../space/Anchor';
import { Coordinate } from '../space/Coordinate';
import { Event, useEventListeners } from '../util/events';
const noop = () => {};

export class Entity {
	public readonly id: string;

	/**
	 * The event that an entity was instructed to moveTo()
	 */
	public readonly moveStart = new Event<[Coordinate]>();

	/**
	 * The event that this entity has completed a moveTo(), according to react-spring's timing
	 */
	public readonly moveRest = new Event<[Coordinate]>();

	/**
	 * The React SVG component that consitutes this entity. Is expected to be defined in a class
	 * that extends `Entity`.
	 */
	public readonly Component: FunctionComponent = () => {
		return null;
	};

	public location: Coordinate;

	/**
	 * The set of behaviour/tasks given to this entity.
	 */
	public job?: Job;

	constructor(id: string, location: Coordinate) {
		this.id = id;
		this.location = location;

		// Movement handling
		this.moveRest.on(loc => {
			this.location = loc;
		});
	}

	public play() {
		return this.job?.start() || noop;
	}

	public doJob(job: Job) {
		this.job = job;

		// @TODO maybe some events
	}

	/**
	 * Move entity directly to a coordinate. Does not consider accessibility or closeness.
	 */
	public moveTo(coordinate: Coordinate) {
		this.moveStart.emit(coordinate);
	}
}

/**
 * A component that automatically transitions the entity component as per its move instructions
 */
export const EntityComponent: FunctionComponent<{ entity: Entity }> = ({ entity }) => {
	const [{ destination, duration }, animatePosition] = useState({
		destination: entity.location,
		duration: 0
	});

	useEventListeners(
		() => [
			// Listen for the entity moveStart order;
			entity.moveStart.on(destination =>
				animatePosition({
					destination,
					duration: entity.location.pythagorasDistanceTo(destination) * 100
				})
			)
		],
		[entity.moveStart]
	);

	const onRest = useCallback(
		() => entity.moveRest.emit(destination),
		[entity.moveRest, destination]
	);

	return (
		<MovingAnchor moveTo={destination} moveSpeed={duration} onRest={onRest}>
			<entity.Component />
		</MovingAnchor>
	);
};
