import { Coordinate } from '../space/Coordinate';
import { Entity } from '../entities/Entity';
import { WorkerEntity } from '../entities/WorkerEntity';
import { OUT_OF_BOUNDS, Terrain } from '../vendor/terrain';

export class Scene {
	public terrain: Coordinate[] = [];
	public entities: Entity[] = [];
	play() {
		const destroyers = this.entities.map(entity => entity.play());
		return () => destroyers.forEach(d => d());
	}
}

export class RandomScene extends Scene {
	private _terrainNoise: Terrain;

	constructor(width: number, height: number) {
		super();

		this._terrainNoise = new Terrain(9);
		this._terrainNoise.generate(1);

		this.terrain = Array.from(new Array(width * height)).map((_, i) => {
			const x = i % width;
			const y = Math.floor(i / width);
			const z = this.terrainAt(x, y);
			return new Coordinate(x, y, (z - 512) / 20);
		});

		this.entities = Array.from(new Array(10)).map((_, i) => {
			const xy = {
				x: Math.floor(Math.random() * width),
				y: Math.floor(Math.random() * height)
			};
			const terrainIndex = xy.y * width + xy.x;
			return new WorkerEntity(
				'entity-' + i,
				Coordinate.clone({
					...xy,
					z: this.terrain[terrainIndex].z
				})
			);
		});
	}
	terrainAt(x: number, y: number) {
		const z = this._terrainNoise.get(x, y);
		if (z === OUT_OF_BOUNDS) {
			throw new Error(`Coordinate (${x}, ${y}) is out of bounds.`);
		}
		return z;
	}
}
