import Color from 'color';
import React, { FunctionComponent } from 'react';

import { CoordArray, PERSPECTIVE } from './PERSPECTIVE';

const BORDER_WIDTH = 1;
const BORDER_NODES: CoordArray[] = [
	[1, 0, 0],
	[1, 1, 0],
	[0, 1, 0],
	[0, 0, 0]
];

let spatialCoordinates = BORDER_NODES.map(coordinate => PERSPECTIVE.toPixels(...coordinate)).map(
	cc => cc.map(c => c + BORDER_WIDTH)
);

export const MonochromeTile: FunctionComponent<
	Omit<React.SVGProps<SVGPolygonElement>, 'fill'> & {
		zoom?: number;
		fill?: Color;
		stroke?: Color;
	}
> = ({
	fill = Color('#282c34'), //.lighten(0.3),

	stroke = fill.darken(0.3).saturate(0.3),
	strokeWidth = BORDER_WIDTH,
	zoom = 1,
	...rest
}) => {
	return (
		<polygon
			points={spatialCoordinates.map(c => c.map(c => c * zoom).join(',')).join(' ')}
			stroke={stroke.string()}
			fill={fill.string()}
			strokeWidth={strokeWidth}
			{...rest}
		/>
	);
};
